<template>
  <div class="gallery">
    <div class="gallery__content">
      <div class="gallery-item" v-for="(img, index) of images" :key="index">
        <el-image
          @click="isModalOpen = true"
          @mousemove="imageCheck(img)"
          fit="cover"
          class="gallery-img"
          :src="img.urls.small"
          :alt="img.alt_description"
        ></el-image>
        <div class="gallery__info" v-if="isViewImage == img.id">
          <div class="gallery__info-title">
            <h4>{{ img.user.name }}</h4>
            <p>@{{ img.user.username }}</p>
          </div>
          <div v-if="img.description" class="gallery__info-image">
            <h3 class="gallery__info-image__title">{{ currentDescription }}</h3>
            <p>{{ img.created_at }}</p>
          </div>
        </div>
      </div>
    </div>
    <Modal
      :image="currentImage"
      @modalfalse="isModalOpen = false"
      v-if="isModalOpen"
      :isModalOpen="isModalOpen"
    />
  </div>
</template>

<script>
import Modal from "@/components/Modal.vue"
import "@/scss/home.scss";

export default {
  components: {
    Modal
  },
  data() {
    return {
      isViewImage: false,
      isModalOpen: false,
      currentImage: "",
      currentDescription: ""
    };
  },
  props: {
    images: Array
  },
  methods: {
    imageCheck(img) {
      this.isViewImage = img.id
      this.currentImage = img
      this.currentDescription = img.description
      if (img.description != null && img.description.length > 100) {
        this.currentDescription = img.description.split("").splice(0, 100).join("") + "..."
      }
    }
  }
};
</script>

<style lang="scss" scoped></style>
