<template>
  <div @click="$emit('modalfalse', false)" class="modal__wrapper">
    <el-card @click.stop.native class="modal">
      <div class="modal__content">
        <div>
          <el-image :alt="image.alt_description" class="modal__image" :src="image.urls.small" fit="cover"></el-image>
          <p class="modal__date">{{ image.created_at }}</p>
        </div>
        <div class="modal__right">
          <h2 class="modal__value">{{ image.user.name }}</h2>
          <p class="modal__value">@{{ image.user.username }}</p>
          <p v-if="image.description" class="modal__value">{{ image.description }}</p>
          <p class="modal__value" v-else>Описание отсутствует</p>
          <el-button @click="imageSave(image)" class="modal__btn" type="success">Скачать изображение</el-button>
        </div>
        <div @click="$emit('modalfalse', false)">
          <i class="modal__icon el-icon-close"></i>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  props: {
    isModalOpen: {
      type: Boolean,
      default: false
    },
    image: Object
  },
  methods: {
    imageSave(img) {
      window.open(img.links.download)
    }
  }
};
</script>

<style lang="scss" scoped>
.modal {
  width: 700px;
  min-height: 400px;
  margin: 40px;
  &__wrapper {
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__image {
    max-height: 300px;
    max-width: 300px;
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    max-width: 700px;
  }
  &__right {
    margin-left: 30px;
    margin-right: 30px;
  }
  &__value {
    padding: 20px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.5);
  }
  &__date {
    margin-top: 20px;
    text-align: center;
  }
  &__btn {
    margin: 20px 0;
  }
  &__icon {
    font-size: 32px;
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 0;
  }
}
</style>
