<template>
  <div>
    <el-header class="header">
      <div class="header__content">
        <div class="header__left">
          <el-link class="header-title" href="/"><h1>MyOceanPhotos</h1></el-link>
          <el-input v-model="textSearch" class="header-input" placeholder="Введите название изображения...">
          </el-input>
          <i @click="findImage" class="header-icon el-icon-position"></i>
        </div>
        <div class="header__right">
          <router-link v-if="!isUser" class="header-link" type="info" to="/auth">Авторизация</router-link>
          <el-link v-if="isUser" type="info" :href="'/users/'+ user.username" >
            <i class="header-icon-profile el-icon-user-solid"></i>
          </el-link>
          <h3 @click="logout" v-if="isUser">Выйти из аккаунта</h3>
        </div>
        <i @click="menuOpen" class="el-icon-menu header__menu-icon"></i>
        <div class="menu" ref="menu">
          <ul class="menu__list">
            <li class="menu-item menu-input"> 
              <el-input @keyup.enter="textSearch" @change="findImage" v-model="textSearch" placeholder="Введите название изображения...">
              </el-input>
              <el-button class="menu-button" @click="findImage" type="success">Найти</el-button>
            </li>
            <li class="menu-item">
              <router-link v-if="!isUser" class="header-link" type="info" to="/auth">
                Авторизация
              </router-link>
              <div class="menu-item-profile">
                <el-link v-if="isUser" type="info" :href="'/users/'+ user.username" >
                  <i class="el-icon-user-solid menu-profile-icon"></i>
                </el-link>
                <h3 @click="logout" v-if="isUser">Выйти из аккаунта</h3>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </el-header>
  </div>
</template>

<script>
import "@/scss/header.scss";

export default {
  data() {
    return {
      isUser: false,
      user: "",
      isOpen: false,
      textSearch: ""
    }
  },
  created() {
    this.checkUser()
  },
  updated() {
    this.checkUser()
  },
  methods: {
    menuOpen() {
      this.isOpen = !this.isOpen;
      if (this.isOpen) this.$refs.menu.classList.add("menu-active")
      if (!this.isOpen && this.$refs.menu.classList.contains("menu-active")) this.$refs.menu.classList.remove("menu-active")
    },
    logout() {
      localStorage.removeItem("Bearer")
      localStorage.removeItem("User")
      this.$message.error("Вы успешно вышли из аккаунта")
      if (this.$route.fullPath != "/") setTimeout(() => this.$router.push("/"), 2000)
      else window.location.reload()
    },
    findImage() {
      if (this.textSearch.length > 2) {
        let text = this.textSearch
        this.textSearch = ""
        this.$router.push("/album?img=" + text)
      }
    },
    checkUser() {
      if (localStorage.getItem("User")) {
        this.isUser = true
        this.user = JSON.parse(localStorage.getItem("User"))
      } else this.isUser = false
    }
  }
};
</script>

<style lang="scss" scoped></style>