<template>
  <div>
    <div v-if="$store.getters.isError.length > 0">
      <Error />
    </div>
    <div v-else class="auth">
      <h1 @click="login">Авторизоваться с помощью Unsplash</h1>
      <img @click="login" src="@/assets/logo.svg" class="auth__logo" alt="" />
      <div class="auth-form" v-if="isAuth">
        <el-input placeholder="Вставьте код авторизации" v-model="codeAuth"></el-input>
        <el-button class="auth-form-btn" type="success" @click="loginEnd">Готово</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { toJson } from "unsplash-js";
import unsplash from "@/plugins/auth-unsplash.js"

export default {
  name: "Auth",
  data() {
    return {
      unsplash: "",
      codeAuth: "",
      isAuth: false
    };
  },
  methods: {
    login() {
      const authenticationUrl = unsplash.auth.getAuthenticationUrl([
        "public",
        "read_user",
        "read_photos",
        "read_collections"
      ]);
      this.isAuth = true
      window.open(authenticationUrl, "_blank");
    },
    loginEnd() {
      unsplash.auth
        .userAuthentication(this.codeAuth)
        .then(toJson)
        .then(json => {
          if (json.error) {
            this.$message.error("Вы ввели неккоректный ключ")
          } else {
            unsplash.auth.setBearerToken(json.access_token);
            localStorage.setItem("Bearer", JSON.stringify(json));
            unsplash.currentUser
              .profile()
              .then(toJson)
              .then(json => {
                localStorage.setItem("User", JSON.stringify(json))
                this.$router.push("/users/" + json.username)
              })
              .catch(err => console.log(err))
          }
        })
        .catch(err => console.log(err))
    }
  },
};
</script>

<style lang="scss" scoped>
.auth {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 700px;
  min-height: 600px;
  padding: 40px 0;
  margin: 0 auto;
  &__logo {
    margin-top: 40px;
    width: 106px;
    height: 106px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    &:hover {
      opacity: 0.6;
    }
  }
  &-form {
    width: 300px;
    margin-top: 50px;
    &-btn {
      width: 100%;
      margin-top: 20px;
    }
  }
}
</style>
