<template>
  <div>
    <div v-if="$store.getters.isError.length > 0">
      <Error />
    </div>
    <div v-else>
      <el-breadcrumb class="nav" separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">Главная страница</el-breadcrumb-item>
        <el-breadcrumb-item>
          <el-link :href="'/album?img=' + keyword">Назад</el-link>
        </el-breadcrumb-item>
        <el-breadcrumb-item>
          <p>Страница альбома</p>
        </el-breadcrumb-item>
      </el-breadcrumb>
    <Images :images="images" />
    </div>
  </div>
</template>

<script>
import Images from "@/components/Images.vue";
import { toJson } from "unsplash-js";
import unsplash from "@/plugins/auth-unsplash";

export default {
  name: "Photos",
  components: { Images },
  data() {
    return {
      images: [],
      keyword: ""
    };
  },
  created() {
    let collectionID = this.$route.params.id;
    this.keyword = this.$route.query.word;
    unsplash.search
      .photos(this.keyword, { collections: collectionID })
      .then(toJson)
      .then(json => this.images = json.results)
      .catch(err => this.$store.commit("setError", err))
  },
};
</script>

<style lang="scss" scoped>
.nav {
  margin: 20px;
}
</style>
