import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isError: []
  },
  mutations: {
    setError(state, err) {
      console.log(err)
      state.isError.push(err)
    }
  },
  actions: {},
  getters: {
    isError: state => state.isError
  },
  modules: {}
});
