<template>
  <div>
    <div class="collections" v-if="collections.length > 0">
      <div v-for="col of collections" :key="col.id">
        <el-card @click.native="getPhotos(col.id)" class="collections__card">
          <div class="collections__left">
            <el-image
              class="collections-img"
              :src="col.cover_photo.urls.small"
              fit="cover"
            >
            </el-image>
            <div class="collections__title">
              <h1>{{ col.title }}</h1>
              <p>{{ col.cover_photo.description }}</p>
            </div>
            <div class="collections__author">
              <h3>{{ col.user.name }}</h3>
              <p>@{{ col.user.username }}</p>
            </div>
          </div>
        </el-card>
      </div>
    </div>
    <div v-if="$store.getters.isError.length > 0">
      <Error />
    </div>
  </div>
</template>

<script>
import { toJson } from "unsplash-js";
import unsplash from "@/plugins/auth-unsplash";
import "@/scss/collections.scss";

export default {
  name: "Collections",
  data() {
    return {
      collectionFind: "",
      collections: [],
    };
  },
  created() {
    this.getCollections()
  },
  updated() {
    this.getCollections()
  },
  methods: {
    getCollections() {
      this.collectionFind = this.$route.query.img;
      unsplash.search
        .collections(this.collectionFind)
        .then(toJson)
        .then((json) => {
          this.collections = json.results;
        })
        .catch((err) => this.$store.commit("setError", err));
    },
    getPhotos(id) {
      this.$router.push("/collection/" + id + "?word=" + this.$route.query.img);
    },
  },
};
</script>

<style lang="scss" scoped></style>
